footer {
    margin-top: auto;
    color: #000;
}
.footer{
    position: relative;
    a, &{
        color: var(--EchoBlue);
    }
    .defLink.h-Flamenco-1{
        display: inline-block;
    }
    h3{
        /* font-size: 17px; */
        font-size: 14px;
        text-transform: uppercase;
        font-weight: 600;
        margin: 0 0 15px;
        a{
            color: var(--White);
            display: inline-block;
            transition: all .3s ease-out;
            &:hover{
                color: var(--EchoBlue);
                transition: all .3s ease-out;
            }
            &::after{
                display: none;
            }
        }
    }
    &__top{
        background-color: var(--Midnight);
        &-inner{
            padding: 50px 0 0;
            &> ul{
                display: flex;
                flex-wrap: wrap;
            }
            .firstStage{
                width: 25%;
                &:first-of-type{
                    width: 50%;
                    ul{
                        display: flex;
                        flex-wrap: wrap;
                        li{
                            width: 50%;
                        }
                    }
                }
                ul{
                    padding-bottom: 30px;
                    li{
                        padding: 7px 15px 7px 0;
                    }
                }
                .contacts{
                    li{
                        padding: 4px 0;
                    }
                    ul{
                        padding-bottom: 25px;
                    }
                    a{
                        display: block;
                        margin-bottom: 30px;
                    }
                    .logo{
                        width: 230px;
                        margin-bottom: 20px;
                    }
                }
                .social-media {
                    display: flex;
                    flex-wrap: wrap;
                    li {
                        padding: 0;
                    }
                    li:not(:last-child) {
                        margin-right: 10px;
                    }
                    a {
                        margin-bottom: 0;
                        transition: all 0.2s;
                        &:hover {
                            opacity: 0.5;
                        }
                    }
                    img {
                        height: 30px;
                        width: 35px;
                        border: none;
                    }
                }
            }
        }
    }
    &__bottom{
        background-color: var(--BlueCharcoal);
        &-inner{
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 22px 0 20px;
            p{
                margin: 0;
            }
        }
    }
    &-fixed{
        display: none;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 5;
        &:hover{
            .Btn-Flamenco{
                background-color: #445f87;
                border-color: #445f87;
            }
        }
    }
}
.anchor{
    position: absolute;
    right: 30px;
    bottom: 107px;
    font-size: 35px;
    color: var(--Flamenco);
    transform: rotate(-90deg);
    border: 1px solid var(--Flamenco);
    border-radius: 10px;
    width: 65px;
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .3s ease-out;
    cursor: pointer;
    &:hover{
        background-color: var(--Flamenco);
        color: var(--White);
        transition: all .3s ease-out;
    }
}
@media(max-width: 1199px){
    .anchor{
        font-size: 27px;
        width: 51px;
        height: 51px;
        right: 15px;
        bottom: 80px;
    }
}
@media(max-width: 1024px){
    .footer{
        &__top{
            &-inner{
                .firstStage{
                    width: 33%;
                    &:first-of-type{
                        width: 66%;
                    }
                    &:last-of-type{
                        margin-left: auto;
                    }
                }
            }
        }
    }
}
@media(max-width: 991px){
    .footer{
        &__top{
            &-inner{
                .firstStage{
                    width: 50%;
                    &:first-of-type{
                        width: 100%;
                    }
                }
            }
        }
    }
}
@media(max-width: 767px){
    .footer{
        h3{
            a::after{
                display: block;
            }
        }
        &__top{
            &-inner{
                padding-top: 47px;
                .firstStage{
                    width: 100%;
                    &:first-of-type{
                        ul {
                            display: none;
                            li{
                                width: 100%;
                            }
                        }
                    }
                    ul{
                        display: none;
                        padding-bottom: 10px;
                    }
                    &:hover{
                        h3{
                            a::after{
                                transform: rotate(180deg);
                            }
                        }
                        ul{
                            display: block;
                        }
                    }
                    .contacts{
                        padding-top: 5px;
                        ul{
                            display: block;
                        }
                    }
                    ul.social-media {
                        display: flex;
                    }
                }
            }
        }
        &__bottom{
            .networks a:not(:last-of-type){
                margin-right: 14px;
            }
        }
    }
}
@media(max-width: 600px){
    .footer-fixed{
        display: flex;
        .Btn-Flamenco{
            width: 100vw;
            border-radius: 0;
        }
    }
    .footer__bottom{
        padding-bottom: 33px;
    }
    .anchor{
        bottom: 112px;
    }
}
@media(max-width: 575px){
    .footer{
        &__top{
            &-inner{
                padding-top: 27px;
            }
        }
    }
}
@media(min-width: 601px){
    .footer-fixed{
        display: none!important;
    }
}